<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data Uji Produk Jadi</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs8>
                        <v-btn depressed dark rounded style="text-transform: none !important;" class="success" @click="dialog = true">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Input Pengujian
                        </v-btn>
                        <v-btn depressed dark rounded style="text-transform: none !important;" class="primary ml-2" to="/laporanujipd" link>
                            <v-icon size="18" class="mr-2">mdi-printer</v-icon>
                            Laporan Uji Produk Jadi
                        </v-btn>
                    </v-flex>
                    <v-flex xs4 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="ujicpkbs" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key='item["No Uji"]'>
                                <td>{{ index + 1 }}</td>
                                <td>{{ item["Nama Produk"] }}</td>
                                <td>{{ item["Kode Produk"] }}</td>
                                <td>{{ item["Jumlah Sampel"] }}</td>
                                <td>{{ item["No Bets"] }}</td>
                                <td>{{ item["Tanggal Pengujian"].substring(0,10) }}</td>
                                <td>{{ item["No Uji"] }}</td>
                                <td>{{ item["Pelaksana"] }}</td>
                                <td>{{ item["Kesimpulan"] }}</td>
                                <td>
                                    <v-btn icon color="indigo" title="Detail" light @click='getDetail(item)'>
                                        <v-icon>mdi-eye</v-icon>
                                    </v-btn>
                                    <!-- <v-btn icon color="indigo" title="Edit" light @click="editHandler(item)">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn> -->
                                    <v-btn icon color="red" title="Edit" light @click="deleteItem(item)">
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>  
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>
        <v-dialog v-model="dialogDetail" persistent max-width="1000px">
            <v-card>
                <v-container grid-list-md mb-0>
                    <v-btn icon  @click="close">
                <v-icon>mdi-close</v-icon>
                </v-btn>
                <h2 class="text-md-center">Detail Spesifikasi Uji Produk Jadi</h2>
                <v-layout row wrap style="margin:10px">
                </v-layout>
                <v-data-table :headers="headersDetailUR" :items="detailuji" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item) in items" :key='item["No UjiR"]'>
                                <td>{{ item["No UjiPD"]}}</td>
                                <td>{{ item["Pemerian"] }}</td>
                                <td>{{ item["Bau"]}}</td>
                                <td>{{ item["Warna"] }}</td>
                                <td>{{ item["PH"]  }}</td>
                                <td>{{ item["Berat Jenis"] }}</td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
            </v-card>
        </v-dialog>
        <v-dialog v-model="dialog" persistent fullscreen>
            <v-card>
                <v-card-title>
                    <span class="headline">Entry Data Pengujian</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="2">
                                <h3 class="mt-2">No Bets</h3>
                            </v-col>
                            <v-col cols="3">
                                 <v-autocomplete single-line outlined dense :items="mutstokcpkbs" class=" mr-4" item-text="No LP"
                                    item-value="No LP" label="No Bet" v-model="noLP" @change="getDataBarang()">
                                 </v-autocomplete>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Nama Produk</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Nama Produk" class=" mr-4"
                                    v-model="mutstokcpkb['Nama Barang']" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Kode Produk</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-autocomplete single-line outlined dense :items="indukBarang" class=" mr-4" item-text="Kode Barang"
                                    item-value="Kode Barang" label="Kode Barang" v-model="kodeBarang" @change="getDataCPKB()">
                                 </v-autocomplete>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Jumlah Sampel</h3>
                            </v-col>
                            <v-col cols="3">
                                 <v-text-field dense single-line outlined type="number" label="Jumlah Sampel"
                                    class=" mr-4" v-model="mutstokcpkb['Jumlah Sampel']" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Tanggal Pengujian</h3>
                            </v-col>
                            <v-col cols="3" style="display: flex">
                                <v-menu ref="menu" v-model="menu" :close-on-content-click="false"
                                    :return-value.sync="date" transition="scale-transition">
                                    <template v-slot:activator="{ on }">
                                        <v-text-field dense single-line outlined v-model="mutstokcpkb['Tanggal Pengujian']"
                                            label="Tanggal Pengujian" class=" mr-4" readonly v-on="on"></v-text-field>
                                    </template>
                                    <v-date-picker v-model="mutstokcpkb['Tanggal Pengujian']" no-title scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                                        <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">No Uji</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Nomor Uji" class=" mr-4"
                                    v-model="mutstokcpkb['No Uji']" required readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Pelaksana</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Pelaksana" class=" mr-4"
                                    v-model="mutstokcpkb['Pelaksana']" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1"></v-col>
                            <v-col cols="2">
                                <h3 class="mt-2">Kesimpulan</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-select dense single-line outlined :items="Keterangan" class=" mr-4" item-text="name"
                                    item-value="kode" label="Kesimpulan" v-model="mutstokcpkb['Kesimpulan']" required>
                                </v-select>
                            </v-col>
                        </v-row>
                        <h2>Spesifikasi Uji</h2><br>
                        <hr> <br>
                        <v-row no-gutters>
                            <v-col cols="1">
                                <h3 class="mt-2">Pemerian</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Pemerian" class=" mr-4"
                                    v-model="formSpesifikasiB.pemerian" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Bau</h3>
                            </v-col>
                            <v-col cols="3">
                                <v-text-field dense single-line outlined label="Bau" class=" mr-4"
                                    v-model="formSpesifikasiB.bau" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Warna</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Warna" class=" mr-4"
                                    v-model="formSpesifikasiB.warna" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="2"></v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">pH</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="pH" class=" mr-4"
                                    v-model="formSpesifikasiB.ph" placeholder="5 - 8,5" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="1">
                                <h3 class="mt-2">Berat Jenis</h3>
                            </v-col>
                            <v-col cols="2">
                                <v-text-field dense single-line outlined label="Berat Jenis" class=" mr-4"
                                    v-model="formSpesifikasiB.beratJenis" placeholder="0,820 - 0,930" required>
                                </v-text-field>
                            </v-col>
                        </v-row>
                        <v-layout align-end justify-end>
                            <v-btn color="red white--text" dense rounded @click="close">Close</v-btn>
                            <v-btn class="ml-4 white--text" dense color="blue darken-1" rounded   @click="setForm()">Save</v-btn>
                        </v-layout>
                    </v-container>
                </v-card-text>
            </v-card>
        </v-dialog>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                kodeBarang: '',
                noLP: '',
                StatusJenis: '',
                statusDetail:'',
                statusUpdate: true,
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                dialog: false,
                dialogDetail:false,
                dialogEdit:false,
                keyword: '',
                headers: [{
                        text: 'No',
                        value: 'no',
                    },
                    {
                        text: 'Nama Produk',
                        value: 'Nama Produk'
                    },
                    {
                        text: 'Kode Produk',
                        value: 'Kode Produk'
                    },
                    {
                        text: 'Jumlah Sampel',
                        value: 'Jumlah Sampel'
                    },
                    {
                        text: 'No Bets',
                        value: 'No Bets'
                    },
                    {
                        text: 'Tanggal Pengujian',
                        value: 'Tanggal Pengujian'
                    },
                    {
                        text: 'No Uji',
                        value: 'No Uji'
                    },
                    {
                        text: 'Pelaksana',
                        value: 'Pelaksana'
                    },
                    {
                        text: 'Kesimpulan',
                        value: 'Kesimpulan'
                    },
                    {
                        text: 'Aksi',
                        value: ''
                    },
                ],
                headersDetailUR: [{
                        text: 'No Uji',
                        value: 'No Uji',
                    },
                    {
                        text: 'Pemerian',
                        value: 'Pemerian'
                    },
                    {
                        text: 'Bau',
                        value: 'Bau'
                    },
                    {
                        text: 'Warna',
                        value: 'Warna'
                    },
                    {
                        text: 'pH',
                        value: 'pH'
                    },
                    {
                        text: 'Berat Jenis',
                        value: 'Berat Jenis'
                    },
                ],
                ujicpkbs: [],
                detailuji: [],
                mutstokcpkbs: [],
                mutstokcpkb: [],
                indukBarang: [],
                snackbar: false,
                color: null,
                text: '',
                load: false,
                formSpesifikasiB: {
                    pemerian: '',
                    bau: '',
                    warna: '',
                    ph: '',
                    beratJenis: '',
                },
                spesifikasiupd: new FormData,
                datauji: new FormData,
                typeInput: 'new',
                errors: '',
                updatedId: '',
                Keterangan: [
                    {
                        name: 'Lulus',
                        kode: 'Lulus'
                    },
                    {
                        name: 'Tidak Lulus',
                        kode: 'Tidak Lulus'
                    },
                ],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'tabelujiprodukjadicpkbs'
                this.$http.get(uri).then(response => {
                    this.ujicpkbs = response.data
                })
            },

            getDataAutoComplete(){
                var uri = this.$apiUrl + 'mutstokcpkbs/header'
                this.$http.get(uri).then(response => {
                    this.mutstokcpkbs = response.data
                })
            },

            getDataCPKB() {
                if(this.noLP){
                    var uri = this.$apiUrl + 'mutstokcpkbs/headerpj/' + this.kodeBarang
                    this.$http.get(uri).then(response => {
                        this.mutstokcpkb = response.data[0]
                        this.mutstokcpkb['No Uji'] = 'UJ' + this.noLP + this.kodeBarang
                        console.log(this.mutstokcpkb)
                    })
                }
                
            },

            getDataBarang() {
                var uri = this.$apiUrl + 'mutstokcpkbs/produksi/' + this.noLP
                this.$http.get(uri).then(response => {
                    this.indukBarang = response.data
                    // this.mutstokcpkb["No Uji"]= 'UR' + this.mutstokcpkb["No LP"]
                })
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },

            // Get Detail Uji
            getDetail(item){
                var uri = this.$apiUrl + 'spesifikasiupdcpkbs/' + item["No Uji"]
                this.$http.get(uri).then(response => {
                    this.detailuji = response.data
                })
                this.dialogDetail = true
            },
            sendData() {
                // console.log(this.formSpesifikasiB)
                // Induk
                this.spesifikasiupd.append('namaProduk', this.mutstokcpkb['Nama Barang']);
                this.spesifikasiupd.append('kodeProduk', this.kodeBarang);
                this.spesifikasiupd.append('jumlahSampel', this.mutstokcpkb['Jumlah Sampel']);
                this.spesifikasiupd.append('noBets', this.noLP);
                this.spesifikasiupd.append('tanggalPengujian', this.mutstokcpkb['Tanggal Pengujian']);
                this.spesifikasiupd.append('noUji', this.mutstokcpkb['No Uji']);
                this.spesifikasiupd.append('pelaksana', this.mutstokcpkb['Pelaksana']);
                this.spesifikasiupd.append('kesimpulan', this.mutstokcpkb['Kesimpulan']);
                var uri = this.$apiUrl + 'tabelujiprodukjadicpkbs'
                this.load = true
                this.$http.post(uri, this.spesifikasiupd).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackbar
                    this.load = false;
                    this.dialog = false
                    this.getData(); 
                    this.resetForm();
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                })
                this.datauji.append('noUjiPD', this.mutstokcpkb['No Uji']);
                this.datauji.append('pemerian',this.formSpesifikasiB.pemerian );
                this.datauji.append('bau', this.formSpesifikasiB.bau);
                this.datauji.append('warna',this.formSpesifikasiB.warna );
                this.datauji.append('ph', this.formSpesifikasiB.ph );
                this.datauji.append('beratJenis', this.formSpesifikasiB.beratJenis);
                var uri = this.$apiUrl + 'spesifikasiupdcpkbs'
                this.load = true
                this.$http.post(uri, this.datauji)
                this.noLP = ''
            },

            updateData() {
                // Edit Data Uji
                this.spesifikasiur.append('namaProduk', this.mutstokcpkb['Nama Barang']);
                this.spesifikasiur.append('kodeProduk', this.mutstokcpkb['Kode Barang']);
                this.spesifikasiur.append('jumlahSampel', this.mutstokcpkb['Jumlah Sampel']);
                this.spesifikasiur.append('noBets', this.noLP);
                this.spesifikasiur.append('tanggalPengujian', this.mutstokcpkb['Tanggal Pengujian']);
                this.spesifikasiur.append('noUji', this.mutstokcpkb['No Uji']);
                this.spesifikasiur.append('pelaksana', this.mutstokcpkb['Pelaksana']);
                this.spesifikasiur.append('kesimpulan', this.mutstokcpkb['Kesimpulan']);
                var uri = this.$apiUrl + 'tabelujiruahancpkbs/' + this.mutstokcpkb['No Uji']
                this.load = true
                this.$http.post(uri, this.spesifikasiur).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackbar
                    this.load = false;
                    this.dialog = false
                    this.getData(); 
                    this.resetForm();
                    this.typeInput = 'new';
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                    this.typeInput = 'new';
                })

                // Edit Detail Uji
                this.datauji.append('noUjiR', this.mutstokcpkb['No Uji']);
                this.datauji.append('pemerian',this.formSpesifikasiB.pemerian );
                this.datauji.append('bau', this.formSpesifikasiB.bau);
                this.datauji.append('warna',this.formSpesifikasiB.warna );
                this.datauji.append('ph', this.formSpesifikasiB.ph );
                this.datauji.append('beratJenis', this.formSpesifikasiB.beratJenis);
                var uri = this.$apiUrl + 'spesifikasiurcpkbs/' + this.mutstokcpkb['No Uji']
                this.load = true
                this.$http.post(uri, this.datauji)
                this.noLP = ''
                
            },
            
            editHandler(item) {
                this.typeInput = 'edit';
                this.statusUpdate = false
                this.mutstokcpkb['Nama Barang'] = item["Nama Produk"],
                this.kodeBarang = item["Kode Produk"],
                this.mutstokcpkb["Jumlah Sampel"] = item["Jumlah Sampel"],
                this.noLP = item["No Bets"],
                this.mutstokcpkb["Tanggal Pengujian"] = item["Tanggal Pengujian"],
                this.mutstokcpkb["No Uji"] = item["No Uji"],
                this.mutstokcpkb["Pelaksana"] = item["Pelaksana"],
                this.mutstokcpkb["Kesimpulan"] = item["Kesimpulan"]
        
                var uri = this.$apiUrl + 'spesifikasiupdcpkbs/' + item["No Uji"]
                this.$http.get(uri).then(response => {
                    this.detailuji = response.data
                    this.formSpesifikasiB.pemerian = this.detailuji[0]["Pemerian"],
                    this.formSpesifikasiB.bau = this.detailuji[0]["Bau"],
                    this.formSpesifikasiB.warna = this.detailuji[0]["Warna"],
                    this.formSpesifikasiB.ph = this.detailuji[0]["PH"],
                    this.formSpesifikasiB.beratJenis = this.detailuji[0]["Berat Jenis"]
                })
                this.dialog = true
            },
            deleteItem(item) { 
                var uri = this.$apiUrl + 'tabelujiprodukjadicpkbs/' + item["No Uji"]; 
                this.$http.delete(uri).then(response => {
                    this.snackbar = true;
                    this.text = response.data;
                    this.color = 'green'
                    this.deleteDialog = false;
                    this.getData();
                    this.Activity('Melakukan Delete Supplier');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                })
                var uri = this.$apiUrl + 'spesifikasiupdcpkbs/' + item["No Uji"]; 
                this.$http.delete(uri)
            },
            setForm() {
                if (this.typeInput === 'new') {
                    this.sendData()
                    
                } else {
                    this.updateData()
                }
            },
            close() {
                this.dialogDetail = false
                this.statusDetail = null
                this.dialogEdit = false
                this.dialog = false
                this.formSpesifikasiB = {
                    pemerian: '',
                    bau: '',
                    warna: '',
                    ph: '',
                    beratJenis: '',
                }
                this.mutstokcpkb = []
                this.noLP = ''
            },
            resetForm() {
                this.formSpesifikasiB = {
                    pemerian: '',
                    bau: '',
                    warna: '',
                    ph: '',
                    beratJenis: '',
                }
                this.mutstokcpkb = [],
                this.statusUpdate = true
                this.noLP = ''
                this.kodeBarang = ''
            }
        },
        mounted() {
            this.getData();
            this.getDataAutoComplete();
        },
    }
</script>